    import { useRef, useState, useEffect } from "react";
    import classNames from "classnames/bind";
    import style from "./UploadImage.module.scss";


    const cx = classNames.bind(style);

    function UploadImage({ Item, name, id, value = "" ,required = false, multiple = false, onImageChange}) {
        // S dng useRef  tham chiu n th input
        const fileInputRef = useRef(null);
        const [images, setImages] = useState([]);

        useEffect(() => {
 
            if (Item) {
                if (Array.isArray(Item)) {
                    // Trng hp Item l mng, setImages vi mng ny
                    const Images = Item.map((url) => ({ preview: url }));
                    setImages(Images);
                } else {
                    // Trng hp Item l chui, setImages vi mng cha 1 phn t
                    setImages([{ preview:  Item }]);
                }
            }
        }, [Item]);

        useEffect(() => {
            if (value === "") {
                // Nu Item l chui rng, xa ht d liu trong mng images
                setImages([]);
            } 
        }, [value]);

        useEffect(() => {
            // cleanup
            return () => {
                images.forEach((image) => URL.revokeObjectURL(image.preview));
            };
        }, [images]);

        // Hm x l khi nt c nhn
        const handleButtonClick = () => {
            // Kch hot s kin click trn th input
            fileInputRef.current.click();
        };

        // Hm x l khi c s thay i trn th input ( chn file)
        const handleFileInputChange = (e) => {
            // Thc hin cc hnh ng x l file  y (v d: hin th tn file  chn)
            const selectedFiles = fileInputRef.current.files;

            const newImages = Array.from(selectedFiles).map((file) => {
            file.preview = URL.createObjectURL(file);
            return file;
            });

            setImages(newImages);
            onImageChange(e.target.files);
        };
        return (
            <section className={cx("wrapper")}>
                {images.map((img, keys) => (
                    <section key={keys} className={cx("content")}>
                        <article className={cx("images")}>                            
                            <img src={img.preview} alt="img" />
                        </article>
                    </section>
                ))}
                    <article className={cx("item")}>
                        <button onClick={handleButtonClick}>
                            Chn {name}
                            {/* Thm ref vo th input  tham chiu n n */}
                            <input
                                ref={fileInputRef}
                                type="file"
                                name={name}
                                id={id}
                                required={required}
                                multiple={multiple}
                                onChange={(e) => handleFileInputChange(e)}
                            />
                        </button>
                    </article>
            </section>
        );
    }

    export default UploadImage;
